.home-page {
  padding: 20px;
  max-width: 100%;
  padding-bottom: 80px;
}

.page-title {
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 1.8rem;
  font-weight: 600;
}

/* Фильтры */
.habit-filters {
  display: flex;
  justify-content: center;
  margin-bottom: 1.75rem;
  gap: 10px;
}

.filter-button {
  padding: 8px 18px;
  background: none;
  border: 1px solid var(--border-color);
  border-radius: 25px;
  color: var(--text-color);
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
}

.filter-button:hover {
  border-color: var(--primary-color);
  transform: translateY(-2px);
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);
}

.dark-mode .filter-button:hover {
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
}

.filter-button.active {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  color: white;
  box-shadow: 0 3px 10px rgba(var(--primary-color-rgb), 0.3);
}

/* Список привычек */
.habits-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
}

.habit-item {
  background-color: var(--bg-secondary);
  border-radius: 16px;
  padding: 1.25rem;
  box-shadow: var(--card-shadow);
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid var(--border-color);
}

.habit-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
}

.dark-mode .habit-item:hover {
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2);
}

.habit-item.completed {
  border-left: 4px solid var(--success-color);
}

.habit-content {
  flex: 1;
}

.habit-title {
  font-size: 1.15rem;
  margin-bottom: 0.6rem;
  font-weight: 600;
  color: var(--text-color);
}

.habit-category {
  display: inline-block;
  padding: 3px 10px;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 12px;
  font-size: 0.85rem;
  color: var(--text-secondary);
  margin-right: 10px;
  font-weight: 500;
}

.dark-mode .habit-category {
  background-color: rgba(255, 255, 255, 0.1);
}

.habit-streak {
  display: inline-block;
  padding: 3px 10px;
  background-color: rgba(var(--primary-color-rgb), 0.12);
  border-radius: 12px;
  font-size: 0.85rem;
  color: var(--primary-color);
  font-weight: 500;
}

.habit-actions {
  display: flex;
  gap: 0.6rem;
}

.action-button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.03);
  border: none;
  cursor: pointer;
  color: var(--text-color);
  transition: background-color 0.3s ease, color 0.3s ease, transform 0.2s ease;
  font-size: 1.1rem;
}

.dark-mode .action-button {
  background-color: rgba(255, 255, 255, 0.05);
}

.action-button:hover {
  background-color: rgba(0, 0, 0, 0.08);
  transform: scale(1.1);
}

.dark-mode .action-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.action-button.complete:hover {
  color: var(--success-color);
  background-color: rgba(76, 175, 80, 0.1);
}

.action-button.undo:hover {
  color: var(--warning-color);
  background-color: rgba(255, 152, 0, 0.1);
}

.action-button.edit:hover {
  color: var(--info-color);
  background-color: rgba(33, 150, 243, 0.1);
}

.action-button.delete:hover {
  color: var(--danger-color);
  background-color: rgba(244, 67, 54, 0.1);
}

/* Кнопка добавления привычки */
.floating-add-button {
  position: fixed;
  bottom: 80px;
  right: 20px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: var(--primary-color);
  color: white;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  box-shadow: 0 4px 15px rgba(var(--primary-color-rgb), 0.4);
  cursor: pointer;
  transition: transform 0.3s ease, background-color 0.3s ease, box-shadow 0.3s ease;
  z-index: 10;
}

.floating-add-button:hover {
  transform: translateY(-5px) scale(1.05);
  background-color: var(--primary-color-dark);
  box-shadow: 0 6px 20px rgba(var(--primary-color-rgb), 0.5);
}

.floating-add-button:active {
  transform: translateY(0) scale(0.95);
  box-shadow: 0 2px 10px rgba(var(--primary-color-rgb), 0.3);
}

/* Пустое состояние */
.empty-state {
  text-align: center;
  padding: 3.5rem 1.5rem;
  background-color: var(--bg-secondary);
  border-radius: 16px;
  margin-top: 1.5rem;
  box-shadow: var(--card-shadow);
  border: 1px solid var(--border-color);
}

.empty-state p {
  margin-bottom: 1.5rem;
  color: var(--text-secondary);
  font-size: 1.1rem;
}

.empty-state .button {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 12px 24px;
  font-size: 1.05rem;
  border-radius: 30px;
  box-shadow: 0 4px 15px rgba(var(--primary-color-rgb), 0.3);
}

/* Состояния загрузки и ошибки */
.loading-container, 
.error-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  text-align: center;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(var(--primary-color-rgb), 0.1);
  border-top-color: var(--primary-color);
  border-radius: 50%;
  animation: spin 1s ease-in-out infinite;
  margin-bottom: 20px;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.error-message {
  color: var(--danger-color);
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  font-weight: 500;
}

/* Адаптивные стили */
@media (min-width: 768px) {
  .home-page {
    max-width: 800px;
    margin: 0 auto;
  }
  
  .habits-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 1.25rem;
  }
} 