.settings-page {
  padding: 20px;
  max-width: 100%;
}

.page-title {
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 1.8rem;
  font-weight: 600;
}

/* Стили пользовательской информации */
.user-info {
  display: flex;
  align-items: center;
  background-color: var(--bg-secondary);
  padding: 1.5rem;
  border-radius: 16px;
  margin-bottom: 1.5rem;
  box-shadow: var(--card-shadow);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid var(--border-color);
}

.user-info:hover {
  transform: translateY(-2px);
}

.user-avatar {
  width: 65px;
  height: 65px;
  border-radius: 50%;
  background-color: var(--primary-color);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.6rem;
  font-weight: bold;
  margin-right: 1.2rem;
  box-shadow: 0 2px 10px rgba(var(--primary-color-rgb), 0.3);
}

.user-details {
  flex: 1;
}

.user-name {
  font-size: 1.3rem;
  font-weight: bold;
  margin-bottom: 0.3rem;
}

.user-username {
  font-size: 0.95rem;
  color: var(--text-secondary);
}

/* Стили секций настроек */
.settings-section {
  background-color: var(--bg-secondary);
  border-radius: 16px;
  padding: 1.2rem;
  margin-bottom: 1.5rem;
  box-shadow: var(--card-shadow);
  border: 1px solid var(--border-color);
}

.section-title {
  font-size: 1.15rem;
  margin-bottom: 1.2rem;
  padding-left: 0.5rem;
  color: var(--text-secondary);
  font-weight: 600;
}

/* Стили элементов настроек */
.setting-item {
  display: flex;
  align-items: center;
  padding: 1.1rem;
  border-radius: 12px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-bottom: 0.5rem;
}

.setting-item:last-child {
  margin-bottom: 0;
}

.setting-item:hover {
  background-color: var(--bg-hover);
  transform: translateX(3px);
}

.setting-icon {
  width: 42px;
  height: 42px;
  border-radius: 50%;
  background-color: rgba(var(--primary-color-rgb), 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  margin-right: 1rem;
  color: var(--primary-color);
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.setting-item:hover .setting-icon {
  transform: scale(1.1);
  background-color: rgba(var(--primary-color-rgb), 0.15);
}

.setting-icon.danger {
  color: var(--danger-color);
  background-color: rgba(244, 67, 54, 0.1);
}

.setting-content {
  flex: 1;
}

.setting-title {
  font-weight: 500;
  margin-bottom: 0.25rem;
  font-size: 1.05rem;
}

.setting-title.danger {
  color: var(--danger-color);
}

.setting-description {
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.setting-arrow {
  font-size: 1.5rem;
  color: var(--text-secondary);
  margin-left: 0.5rem;
}

/* Стили переключателя */
.setting-toggle {
  margin-left: 1rem;
}

.toggle-switch {
  width: 52px;
  height: 28px;
  background-color: rgba(0, 0, 0, 0.15);
  border-radius: 14px;
  position: relative;
  transition: background-color 0.3s ease;
}

.dark-mode .toggle-switch {
  background-color: rgba(255, 255, 255, 0.15);
}

.toggle-switch.active {
  background-color: var(--primary-color);
}

.toggle-knob {
  width: 24px;
  height: 24px;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  top: 2px;
  left: 2px;
  transition: transform 0.3s ease;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
}

.toggle-switch.active .toggle-knob {
  transform: translateX(24px);
}

/* Стили информации о приложении */
.app-info {
  text-align: center;
  padding: 1.5rem;
  color: var(--text-secondary);
  font-size: 0.85rem;
  margin-top: 2rem;
}

.app-version {
  margin-bottom: 0.5rem;
}

/* Адаптивные стили */
@media (min-width: 768px) {
  .settings-page {
    max-width: 600px;
    margin: 0 auto;
  }
}

@media (max-width: 480px) {
  .settings-page {
    padding: 15px;
  }
  
  .user-avatar {
    width: 50px;
    height: 50px;
    font-size: 1.2rem;
  }
  
  .setting-icon {
    width: 38px;
    height: 38px;
    font-size: 1rem;
  }
} 